body,
#root {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}

li {
    list-style: none;
    width: 180px;
}

.HolyGrail-body {
    display: flex;
    flex: 1;
}

.HolyGrail-content {
    flex: 1;
}

.HolyGrail-nav,
.HolyGrail-ads {
    /* 12em is the width of the columns */
    flex: 0 0 12em;
}

.HolyGrail-nav {
    /* put the nav on the left */
    order: -1;
}

header,
footer {
    background-color: aliceblue;
    line-height: 5rem;
    padding: 0 1rem;
}

main,
nav,
aside {
    padding: 1rem;
}

footer {
    line-height: 3rem;
}